
export type Message = {
  id: string;
  content: string;
  recipient: string;
  timestamp: number;
};

export type Recipient = {
    name: string;
    messageCount: number;
}

// In-memory store for messages.
// NOTE: This is for demonstration purposes only. In a real application,
// you would use a persistent database like Firestore.
const messages: Message[] = [
    {
        id: '1',
        content: 'Welcome to Message in a Bottle! This is a sample message to get you started. Try sending one to a friend named "alex".',
        recipient: 'alex',
        timestamp: new Date('2023-10-26T10:00:00Z').getTime(),
    },
    {
        id: '2',
        content: 'You can use the AI polishing tool to make your messages shine before you send them.',
        recipient: 'alex',
        timestamp: new Date('2023-10-26T10:05:00Z').getTime(),
    },
    {
        id: '3',
        content: 'Here is another message for someone else.',
        recipient: 'sam',
        timestamp: new Date('2023-10-27T11:00:00Z').getTime(),
    },
];

export function addMessage(content: string, recipient: string): Message {
  const newMessage: Message = {
    id: crypto.randomUUID(),
    content,
    recipient: recipient.toLowerCase().trim(),
    timestamp: Date.now(),
  };
  // Add to the beginning of the array to show newest first
  messages.unshift(newMessage);
  return newMessage;
}

export function getMessagesForRecipient(recipient: string): Message[] {
  return messages.filter(
    (msg) => msg.recipient === recipient.toLowerCase().trim()
  );
}

export function getMessageById(id: string): Message | undefined {
    return messages.find((msg) => msg.id === id);
}

export function getRecipients(): Recipient[] {
    const recipientMap = new Map<string, number>();
    messages.forEach(msg => {
        recipientMap.set(msg.recipient, (recipientMap.get(msg.recipient) || 0) + 1);
    });

    return Array.from(recipientMap.entries()).map(([name, messageCount]) => ({
        name,
        messageCount,
    }));
}
